import Vue from 'vue'
import Router from 'vue-router'
import puyitou from './puyitou'
import shop from './shop'
import puyipay from './puyipay'
import ttl from './ttl'

Vue.use(Router)

export default new Router({
  routes: [
    ...puyitou.router, // 
    ...shop.router, // 
    ...puyipay.router,  // 
    ...ttl.router // 
  ]
})



